<template>
  <div class="return" @click="returnGo">
    <van-icon name="arrow-left" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      address_id: ""
    };
  },
  props: {
    addressId: ""
  },
  methods: {
    returnGo() {
      this.$router.push("/cart?address_id=" + this.addressId);
    }
  }
};
</script>

<style lang="less" scoped>
.return {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 95%;
  background-color: var(--themeColor);
  text-align: left;
  font-size: 24px;
  display: flex;
  align-items: center;
  color: aliceblue;
}
</style>