<template>
  <div class="contain_wrap">
    <Return :addressId="radio"></Return>
    <div class="address_btn">
      <router-link to="/address">
        <van-button type="primary" plain>添加地址</van-button>
      </router-link>
    </div>
    <!-- 地址列表 -->
    <van-radio-group v-model="radio">
      <van-swipe-cell v-for="(item,index) in addresslist" :key="index">
        <van-cell clickable @click="radio = index">
          <template #right-icon>
            <div class="user_info">
              <span class="user_name">{{item.userName}}</span>
              <span class="user_tel">{{item.telNumber}}</span>
              <div class="user_place">{{item.all+item.detailed}}</div>
            </div>
            <van-radio checked-color="red" :name="index" />
          </template>
        </van-cell>
        <template slot="right">
          <van-button square type="danger" text="删除" />
        </template>
      </van-swipe-cell>
    </van-radio-group>
  </div>
</template>

<script>
import Return from "../components/Return";
export default {
  data() {
    return {
      addresslist: [],
      radio: 0
    };
  },
  created() {
    this.addresslist = JSON.parse(localStorage.getItem("addressArray"));
  },
  components: { Return }
};
</script>

<style lang="less" scoped>
.contain_wrap {
  background-color: var(--threeColor);
  padding: 10px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  .address_btn {
    margin: 40px 0;
    padding: 20px 0;
    display: block;
    background-color: #fff;
  }
  .van-radio-group {
    .van-swipe-cell {
      .van-cell {
        padding: 5px;
        margin: 2px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align-last: left;
      }
    }
  }
}
</style>